        position: absolute;
        right: 100%;
        top: 15px;
        border: solid transparent;
        border-right-color: #d2d6de;
        content: ' ';
        height: 0;
        width: 0;
        pointer-events: none;
}
.direct-chat-text:after {
        border-width: 5px;
        margin-top: -5px;
}
.direct-chat-text:before {
